html {
  font-family: "Rubik", sans-serif;
  font-size: 62.5%;
}

body {
  background-image: var(--img-url);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-color: var(--color-bg);
  color: var(--color-text);
  min-height: 100dvh;
  overflow-x: hidden;
  padding: 0 10%;
  width: 100%;
  display: grid;
  grid-template-rows: 25% 1fr 15%;
}
body .dark {
  color-scheme: dark;
}
body .light {
  color-scheme: light;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__subject {
  display: flex;
  gap: 3.2rem;
  align-items: center;
}
.header__subject img {
  background-color: rgba(var(--color-quiz--icon), 0.05);
  border-radius: 8px;
  padding: 0.8rem;
}
.header__subject p {
  font-size: 2.8rem;
}

.theme {
  display: flex;
  align-items: center;
  gap: 1.6rem;
}
.theme__icon--sun {
  height: 2.4rem;
  width: 2.4rem;
  background-image: var(--img-icon--sun);
  background-repeat: no-repeat;
}
.theme__icon--moon {
  height: 2.4rem;
  width: 2.4rem;
  background-image: var(--img-icon--moon);
  background-repeat: no-repeat;
}
.theme__btn {
  position: relative;
  height: 2.8rem;
  width: 4.8rem;
  background-color: #a729f5;
  cursor: pointer;
}
.theme__btn::before {
  position: absolute;
  content: "";
  height: 2rem;
  width: 2rem;
  top: 50%;
  transform: translateY(-50%);
  background-color: white;
  border-radius: 100%;
  transition: left 0.3s ease, right 0.3s ease;
}

.dark .theme__btn::before {
  right: 2px;
}

.light .theme__btn::before {
  left: 2px;
}

.container {
  display: none;
}
.container.show {
  display: grid;
  gap: 3.2rem;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  color: var(--color-text);
}

.home {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
.home h1 {
  font-size: 6.4rem;
}
.home p {
  font-size: 2rem;
  color: var(--color-text--secondary);
}

.subjects {
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}
.subjects__subject {
  display: flex;
  align-items: center;
  gap: 3.2rem;
  background-color: var(--color-quiz);
  padding: 1.2rem;
  border-radius: 8px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  border: 3px solid transparent;
}
.subjects__subject:hover {
  border: 3px solid rgba(var(--color-option), 0.5);
  color: rgba(var(--color-option), 0.5);
  transition: all 0.3s ease;
}
.subjects__subject:focus, .subjects__subject:active {
  border: 3px solid rgba(var(--color-option), 1);
  color: rgba(var(--color-option), 1);
}
.subjects__subject img {
  background-color: rgba(var(--color-quiz--icon), 0.05);
  border-radius: 8px;
  padding: 0.8rem;
}
.subjects__subject p {
  font-size: 2.8rem;
}

.quiz {
  display: none;
}
.quiz.show {
  display: grid;
  gap: 3.2rem;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  color: var(--color-text);
}
.quiz__home {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
.quiz__home h2 {
  font-size: 6.4rem;
}
.quiz__home p {
  font-size: 2rem;
  color: var(--color-text--secondary);
}
.quiz__answers {
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}
.quiz__option {
  background-color: rgba(var(--color-quiz--icon), 0.05);
  border-radius: 8px;
  padding: 1.6rem;
  font-weight: bold;
}
.quiz__answer {
  display: flex;
  align-items: center;
  gap: 3.2rem;
  background-color: var(--color-quiz);
  padding: 1.2rem;
  border-radius: 8px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  border: 3px solid transparent;
}
.quiz__answer:hover .quiz__option {
  background-color: rgba(var(--color-option), 0.1);
  color: rgb(var(--color-option));
  transition: all 0.3s ease;
}
.quiz__answer p {
  font-size: 1.6rem;
}

.btn {
  height: 9.2rem;
  width: 100%;
  outline: none;
  border-style: none;
  color: white;
  background-color: rgb(var(--color-option));
  font-size: 2.8rem;
  border-radius: 8px;
  cursor: pointer;
}
.btn:hover {
  filter: brightness(1.2);
  transition: all 0.3s;
}
.btn:active {
  border-style: none;
  filter: brightness(1.3);
  transition: all 0.3s;
}

body.dark {
  --color-text: white;
  --color-text--secondary: #abc1e1;
  --color-bg: #313e51;
  --color-quiz: #3b4d66;
